import React, {Component} from "react";
import OptionsPageHeader from "./OptionsPageHeader";
import OptionsPageFooter from "./OptionsPageFooter";

import Popup from 'reactjs-popup';
import './manage.css'
import 'reactjs-popup/dist/index.css';

import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faSearch, faTimes} from '@fortawesome/fontawesome-free-solid';


class TableMediaRow extends Component {
    constructor(props) {
        super(props);
        this.state = {...props};
        this.click = this.click.bind(this);
    }

    click() {
        this.props.method(this.state);
    }

    render() {
        /* eslint-disable no-undef */
        const {image, name} = this.state;
        return (
            <div className="table-media-row">
                <img src={image} alt={name} loading="lazy"/>
                <h3>{name}</h3>
                <button onClick={this.click} className="btn-media">{chrome.i18n.getMessage("delete")} </button>
            </div>
        );
    }
}

const utilizeFocus = () => {
    const ref = React.createRef(),
        setFocus = () => {
            ref.current && ref.current.focus()
        }
    return {setFocus, ref}
}


class Manage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            collection: {},
            size: 3,
            myCollection: {},
            favorites: [],
            rotator: {
                time: 30,
                request: 10,
                status: false,
                type: 'time'
            },
            seen: false,
            searchBox: false,
            query: "",
        };

        this.inputFocus = utilizeFocus();

        this.deleteCollection = this.deleteCollection.bind(this);
        this.deletePack = this.deletePack.bind(this);
        this.deletePackOnFav = this.deletePackOnFav.bind(this);


        this.searchFilter = this.searchFilter.bind(this);
        this.toggleSearch = this.toggleSearch.bind(this);

        this.rotatorChangerType = this.rotatorChangerType.bind(this);
        this.rotatorChanger = this.rotatorChanger.bind(this);
        this.rotatorChangerValue = this.rotatorChangerValue.bind(this);

        this.updataStateRotator = this.updataStateRotator.bind(this)


        this.popupOnClose = this.popupOnClose.bind(this)
        this.popupOnOpen = this.popupOnOpen.bind(this);

        chrome.storage.onChanged.addListener((changes, area) => {
            chrome.storage.local.get(['collection', 'size', 'myCollection', 'favorites', 'rotator'], (items) => {
                this.setState({
                    collection: items.collection,
                    size: items.size,
                    myCollection: items.myCollection,
                    favorites: items.favorites,
                    rotator: items.rotator
                });
            })
        });


    }

    toggleSearch(e) {
        this.setState({searchBox: !this.state.searchBox});
        const {searchBox} = this.state;
        if (searchBox === false) {
            setTimeout(() => {
                this.inputFocus.setFocus()
            }, 200)
        }
        e.preventDefault()
    }

    deleteCollection(e) {
        /* eslint-disable no-undef */
        const slug = e.currentTarget.dataset.slug;
        let {collection} = this.state;
        delete collection[slug];
        this.setState({collection: collection});
        chrome.storage.local.set({collection: collection});
        e.preventDefault();
    }

    searchFilter = (event) => {
        this.setState({query: event.target.value.toLowerCase()});
    }

    componentDidMount() {
        /* eslint-disable no-undef */
        chrome.storage.local.get(['collection', 'myCollection', 'favorites', 'size', 'rotator'], (items) => {
            this.setState({
                collection: items.collection,
                size: items.size,
                myCollection: items.myCollection,
                favorites: items.favorites,
                rotator: items.rotator
            });
        })
    }

    //Удаление пака
    deletePack(data) {
        let {collection, favorites} = this.state;
        if (favorites.includes(data.id)) {
            favorites = favorites.filter(e => e !== data.id);
        }
        collection[data.collection].items = collection[data.collection].items.filter(function (element, index) {
            return element.id !== data.id
        });
        if (collection[data.collection].items.length <= 0) {
            delete collection[data.collection];
        }
        this.setState({collection: collection, favorites: favorites});
        chrome.storage.local.set({collection: collection, favorites: favorites});
    }

    rotatorChanger() {
        /* eslint-disable no-undef */
        let {rotator, favorites} = this.state;
        rotator.status = !rotator.status;
        if (favorites.length === 0) {
            rotator.status = false;
        }
        this.setState({rotator: rotator});
        this.updataStateRotator();
    }

    deleteFavorites() {
        let {rotator} = this.state;
        rotator.status = false;
        this.setState({favorites: [], rotator: rotator});
        chrome.storage.local.set({favorites: []})
        this.updataStateRotator();
    }
    updataStateRotator(){
        const {rotator} = this.state;
        chrome.storage.local.set({rotator: rotator});
        chrome.runtime.sendMessage({action: 'rotator', data: rotator});

    }

    rotatorChangerType() {
        const { rotator } = this.state;
        if (rotator.type === 'request') {
            rotator.type = 'time';
        } else {
            rotator.type = 'request';
        }

        this.setState({rotator: rotator});
        this.updataStateRotator();
    }

    rotatorChangerValue(e) {
        let regexp = /^[0-9\b]+$/;
        let event = new Event('input', {bubbles: true});
        this.myinput.dispatchEvent(event);
        if (this.myinput.value !== "" || regexp.test(this.myinput.value)) {
            let {rotator} = this.state;
            if (rotator.type === 'request') {
                rotator.request = this.myinput.value.replace(/\+|-/ig, '');
                rotator.request = parseInt(rotator.request)
            } else {
                rotator.time = this.myinput.value.replace(/\+|-/ig, '');
                rotator.time = parseInt(rotator.time)
            }
            this.setState({rotator: rotator});
            this.updataStateRotator();
        }
    }

    popupOnClose(){
        document.body.style.overflow = 'unset';
        this.setState({query: '', searchBox: false});
    }
    popupOnOpen(){
        document.body.style.overflow = 'hidden';
    }

    deletePackOnFav(id) {
        let {favorites, rotator} = this.state;

        favorites = favorites.filter(val => val !== id);
        if (favorites.length === 0) {
            rotator.status = false;
        }
        this.setState({favorites: favorites, rotator: rotator});
        chrome.storage.local.set({favorites: favorites});
        this.updataStateRotator();

    }

    render() {
        let {collection, favorites, query, searchBox, rotator} = this.state,
            fav = [];
        /* eslint-disable no-undef */

        let html = Object.keys(collection).map(function (key) {
            let item = collection[key];
            let packs = [];
            if (query.length > 1) {
                let tmp = item.items.filter((pack) => {
                    try {
                        return pack.name.toLowerCase().indexOf(query) > -1 || item.name.toLowerCase().indexOf(query) > -1
                    } catch (e) {
                        return "";
                    }
                });
                if (tmp.length > 0) {
                    packs = tmp.map((pack) => {
                        if (favorites.includes(pack.id)) fav.push(pack)
                        pack.collection = key;
                        return <TableMediaRow {...pack}
                                              method={this.deletePack}
                                              onRef={ref => (this.child = ref)}
                                              key={pack.id}/>
                    })
                } else {
                    packs = []
                }
            } else {
                packs = item.items.map((pack) => {
                    if (favorites.includes(pack.id)) {
                        fav.push(pack)
                    }
                    pack.collection = key;
                    return <TableMediaRow {...pack}
                                          method={this.deletePack}
                                          onRef={ref => (this.child = ref)}
                                          key={pack.id}/>
                })
            }
            return <div className="col-lg-4 col-md-6 mb20">
                <div className="c-item">
                    <h3 className="item-title">
                        <span className="fixture">{item.name}</span>
                    </h3>
                    <p className="item-img fixture">
                        <picture>
                            <source type="image/png" srcSet={item.image}/>
                            <img src={item.image} alt={item.name} loading="lazy"/>
                        </picture>
                    </p>

                    <div className="c-btn">
                        <button onClick={this.deleteCollection} data-slug={item.slug}
                                className="btn btn__white">{chrome.i18n.getMessage("delete")}</button>
                        <Popup
                            position="center center"
                            modal
                            nested
                            onClose={this.popupOnClose}
                            onOpen={this.popupOnOpen}
                            trigger={<a className="btn btn__blue"> {chrome.i18n.getMessage('nav_manage')} </a>}>
                            {close => (
                                <div className="modal popup-view">
                                    <div className="popup-view-content">
                                        <h2 className="block-title">{item.name}</h2>
                                        <div className="box-view-search">
                                            {searchBox === true ?
                                                <div className="popup-view-search active">
                                                    <form action="">
                                                        <input type="text"
                                                               ref={this.inputFocus.ref}
                                                               onChange={this.searchFilter}
                                                               placeholder={chrome.i18n.getMessage('enter_your_search_text')}
                                                               name="search" id="search-collname"/>
                                                    </form>
                                                    <span className="view-search-close"
                                                          title={chrome.i18n.getMessage('close')}
                                                          onClick={this.toggleSearch}>
                                                        <FontAwesomeIcon icon={faTimes}/>
                                                    </span>
                                                </div>
                                                : <span className="view-search-btn"
                                                        onClick={this.toggleSearch}
                                                        title={chrome.i18n.getMessage('search')}>
                                                    <FontAwesomeIcon icon={faSearch}/>
                                                </span>
                                            }
                                        </div>
                                        <div className="table-media">
                                            {packs}
                                        </div>
                                    </div>
                                    <div className="popup-view-bot">
                                        <div>
                                            <button
                                                data-slug={item.slug}
                                                onClick={(e) => {
                                                    this.deleteCollection(e);
                                                    this.setState({query: ''});
                                                    close();
                                                }}
                                                className="btn btn__white">{chrome.i18n.getMessage("delete_all")}</button>
                                        </div>
                                        <div>
                                            <span onClick={() => {
                                                this.setState({query: ''});
                                                close()
                                            }}
                                                  className="btn btn__blue popup-view-btn">{chrome.i18n.getMessage("close")}</span>
                                        </div>


                                    </div>

                                    <span onClick={() => {
                                        this.setState({query: ''});
                                        close()
                                    }} className="fancybox-close-small" title="Close">
                                        <FontAwesomeIcon icon={faTimes}/>
                                    </span>
                                </div>
                            )}
                        </Popup>


                    </div>
                </div>
            </div>;
        }.bind(this));


        let favHtml = fav.map((pack) => {
            return <div className="table-media-row">
                <img src={pack.image} alt="Image Cursor Pack" loading="lazy"/>
                <h3>{pack.name}</h3>
                <button onClick={(e) => this.deletePackOnFav(pack.id)}
                        className="btn-media">{chrome.i18n.getMessage("delete")} </button>
            </div>
        })

        let favorite_title_manage_favorites = chrome.i18n.getMessage('favorite_title_manage_favorites');
        let favoriteHtml = <section className="block-favorites">
            <div className="container">
                <h2 className="block-title block-title__line" dangerouslySetInnerHTML={{__html: favorite_title_manage_favorites}} ></h2>
                <div className="favorites-row">
                    <label className="favorites-switch favorites-switch__change">
                        <input type="checkbox" id="rotator_status" checked={rotator.status}
                               onClick={this.rotatorChanger}/>
                        <span
                            className="favorites-switch-name">{chrome.i18n.getMessage('favorite_rotate_favorites')}</span>
                        <span className="favorites-switch-btn"><span className="favorites-circle"></span></span>
                    </label>
                    <label className="favorites-switch favorites-switch__no-change">
                        <input type="checkbox" id="rotator_type" checked={rotator.type === 'request'}
                               onClick={this.rotatorChangerType}
                               disabled={rotator.status === false}/>
                        <span className="favorites-switch-name">{chrome.i18n.getMessage("favorite_by_time")}</span>
                        <span className="favorites-switch-btn"><span className="favorites-circle"></span></span>
                        <span
                            className="favorites-switch-name">{chrome.i18n.getMessage('favorite_by_visited_pages')}</span>
                    </label>
                    <label className="favorites-num">
                        <input type="number" value={rotator.type === 'request' ? rotator.request : rotator.time}
                               id="rotator_type_count"
                               pattern='[0-9]{0,5}'
                               inputMode="numeric"
                               onChange={(e) => {
                                   this.rotatorChangerValue(e)
                               }}
                               ref={(input) => this.myinput = input}
                               disabled={rotator.status === false}/>
                        <span>{rotator.type === 'request' ? 'page' : 'sec'}</span>
                    </label>
                </div>
                {(favorites.length > 0) ? <div className="favorites-btn">
                    <Popup
                        position="center center"
                        modal
                        nested
                        onClose={this.popupOnClose}
                        onOpen={this.popupOnOpen}
                        trigger={<button id="show_favorites"
                                         className="btn btn__blue">{chrome.i18n.getMessage("favorite_manage_button")}</button>}>
                        {close => (
                            <div className="modal popup-view">
                                <div className="popup-view-content">
                                    <h2 className="block-title">Favorites</h2>
                                    <div className="box-view-search">
                                        {searchBox === true ?
                                            <div className="popup-view-search active">
                                                <form action="">
                                                    <input type="text"
                                                           ref={this.inputFocus.ref}
                                                           onChange={this.searchFilter}
                                                           placeholder={chrome.i18n.getMessage('enter_your_search_text')}
                                                           name="search" id="search-collname"/>
                                                </form>
                                                <span className="view-search-close"
                                                      title={chrome.i18n.getMessage('close')}
                                                      onClick={this.toggleSearch}>
                                                    <FontAwesomeIcon icon={faTimes}/>
                                                </span>
                                            </div>
                                            : <span className="view-search-btn"
                                                    onClick={this.toggleSearch}
                                                    title={chrome.i18n.getMessage('search')}>
                                                <FontAwesomeIcon icon={faSearch}/>
                                            </span>
                                        }
                                    </div>
                                    <div className="table-media">
                                        {favHtml}
                                    </div>
                                </div>
                                <div className="popup-view-bot">
                                    <div>
                                        <button
                                            data-slug='favorites'
                                            onClick={(e) => {
                                                this.deleteFavorites(e);
                                                this.setState({query: ''});
                                                close();
                                            }}
                                            className="btn btn__white">{chrome.i18n.getMessage("delete_all")}</button>
                                    </div>
                                    <div>
                                        <span onClick={() => {
                                            this.setState({query: ''});
                                            close()
                                        }}
                                              className="btn btn__blue popup-view-btn">{chrome.i18n.getMessage("close")}</span>
                                    </div>
                                </div>
                                <span onClick={() => {
                                    this.setState({query: ''});
                                    close()
                                }} className="fancybox-close-small" title="Close">
                                    <FontAwesomeIcon icon={faTimes}/>
                                </span>
                            </div>
                        )}
                    </Popup>
                </div> : ""}

            </div>
        </section>;
        let manage_manage_collection = chrome.i18n.getMessage('manage_manage_collection');
        return <div>
            <OptionsPageHeader active='manage'/>
            {favoriteHtml}
            <section className="block-items block-items__pb100 block-items__page">
                <div className="container">
                    <h2 className="block-title block-title__line" dangerouslySetInnerHTML={{__html: manage_manage_collection}} ></h2>
                    <div className="row" id="collection">
                        {html}
                    </div>
                </div>
            </section>
            <OptionsPageFooter/>
        </div>;
    }
}

export default Manage;


