import React, {Component} from 'react';

export default class CursorCardMy extends Component {
    constructor(props,) {
        super(props);
        this.state = {...props};
        this.handleClick = this.handleClick.bind(this);
        /* eslint-disable no-undef */
        this.defaultImageCursor = chrome.extension.getURL('/assets/images/icons/cursor-icon.png');
        this.defaultImagePointer = chrome.extension.getURL('/assets/images/icons/pointer-icon.png');
    }

    handleClick() {
        this.props.rerenderCursor(this.state);
    }

    render() {
        /* eslint-disable no-undef */
        const {name, cursor, pointer} = this.state,
            style = {width: 20, height: 20};
        return (
            <div className="cursor" onClick={this.handleClick}
                 style={{"grid-template-columns": "24px 24px", display: "grid"}}>
                <img src={cursor.path ? cursor.path : this.defaultImageCursor}
                     alt={name} style={style}/>
                <img src={pointer.path ? pointer.path : this.defaultImagePointer}
                     alt={name} style={style}/>
            </div>
        );
    }
}



