import React, {Component} from "react";
import OptionsPageHeader from "./OptionsPageHeader"
import OptionsPageFooter from "./OptionsPageFooter";
import BoxUpload from "../card/box";

class Options extends Component {
    constructor(props) {
        super(props);
        this.state = {
            myCollection: {},
            latestKey: 0,
            upload: {
                cursor: {},
                pointer: {}
            },
        }
        //this.state = {...props};
        this.fileRef = React.createRef();
        this.handleChange = this.handleChange.bind(this);
        this.setNativeInput = this.setNativeInput.bind(this);
        this.setCurrent = this.setCurrent.bind(this)
        this.saveState = this.saveState.bind(this)
        this.setState({latestKey: Object.keys(this.state.myCollection).length + 1})
    }

    componentDidMount() {
        /* eslint-disable no-undef */
        chrome.storage.local.get(['myCollection'], (items) => {
            this.setState({
                myCollection: items.myCollection,
                latestKey: (Object.keys(items.myCollection).length) + 1
            });
        })
    }

    async saveState(pack) {
        /* eslint-disable no-undef */
        if (pack.id != null) {
            let {myCollection} = this.state;
            myCollection[pack.id] = pack;
            if (!pack.cursor.path && !pack.pointer.path) {
                delete myCollection[pack.id];
            }
            this.setState({myCollection: myCollection});
            chrome.storage.local.set({myCollection: myCollection})
        } else {
        }
    }

    toBase64 = file => new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    })

    handleChange(event) {
        let {myCollection, upload} = this.state,
            pack = myCollection[upload.id];
        if (event.target.files.length) {
            if (!pack) {
                pack = {
                    id: upload.id,
                    cursor: {},
                    pointer: {}
                }
            }
            this.toBase64(event.target.files[0]).then((res) => {
                pack[upload.type].path = res;
                myCollection[upload.id] = pack;
                this.setState({myCollection: myCollection});
                chrome.storage.local.set({myCollection: myCollection});
            })

        }
        event.preventDefault();
    }

    setCurrent(type, id) {
        this.setState({upload: {id: id, type: type}});
    }

    setNativeInput(type, create, packid = new Date().getTime()) {
        this.setState({
            upload: type, is_new: create, editingId: packid,
            latestKey: (Object.keys(this.state.myCollection).length)++
        });
        this.fileRef.current.click()
    }

    render() {
        /* eslint-disable no-undef */
        const pageName = chrome.i18n.getMessage("options_upload_your_own_cursors"),
            options_upload_recommendations = chrome.i18n.getMessage("options_upload_recommendations");

        let {myCollection, latestKey} = this.state,
            newUpload = <BoxUpload
                fileRef={this.fileRef}
                key={'new' + latestKey}
                cursor={{}}
                pointer={{}}
                saveState={this.saveState}
                setCurrent={this.setCurrent}/>,
            boxCollections = Object.keys(myCollection).map(function (key) {
                let pack = myCollection[key];
                return <BoxUpload {...pack} id={key} fileRef={this.fileRef}
                                  key={key}
                                  saveState={this.saveState}
                                  setCurrent={this.setCurrent}/>
            }.bind(this));
        return (<div>
            <OptionsPageHeader active='options'/>
            <section className="block-upload">
                <div className="container">
                    <h1 className="block-title block-title__line mb20"
                        dangerouslySetInnerHTML={{__html: pageName}}></h1>
                    <div className="upload-text"
                         dangerouslySetInnerHTML={{__html: options_upload_recommendations}}></div>
                    <input type="file" id="upload" accept="image/*" style={{display: "none"}} ref={this.fileRef}
                           onChange={this.handleChange}/>
                    <div className="box-upload">
                        <div className="row">
                            {boxCollections}
                            {newUpload}
                        </div>
                    </div>
                </div>
            </section>
            <OptionsPageFooter/>
        </div>);
    }
}

export default Options;


