import React, {Component} from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import { faPlus, faTrash} from "@fortawesome/fontawesome-free-solid";

export default class BoxUpload extends Component {
    constructor(props) {
        super(props);
        this.state = {};
        this.state = {...props};

        this.setNativeInput = this.setNativeInput.bind(this);
        this.deleteCursor = this.deleteCursor.bind(this);
        this.deletePointer = this.deletePointer.bind(this);

    }
    componentWillMount(){

    }
    deleteCursor() {
        this.setState({cursor: {}});
        const {id, pointer, name} = this.state;
        this.props.saveState({id: id, cursor: {}, pointer: pointer, name: name})
    }

    deletePointer() {
        this.setState({pointer: {}});
        const {id, cursor, name} = this.state;
        this.props.saveState({id: id, cursor: cursor, pointer: {}, name: name})
    }

    setNativeInput(type, id) {
        if(id === undefined){
            id = new Date().getTime();
        }
        this.props.setCurrent(type, id)
        this.state.fileRef.current.click();
    }

    render() {
        const {cursor, pointer, id} = this.state;
        /* eslint-disable no-undef */
        const cursorContent = <div className="preview-cur before-n">
            <img src={cursor.path ? cursor.path : "/assets/images/icons/cursor-icon.png"} alt="Cursor" onClick={() => this.setNativeInput('cursor', id)}/>
            <a className="button action button__upload"
               onClick={() => !cursor.path ? this.setNativeInput('cursor', id) : this.deleteCursor()}
               title={chrome.i18n.getMessage('hint_upload_cursor')}>
                {cursor.path ? <FontAwesomeIcon icon={faTrash}/> : <FontAwesomeIcon icon={faPlus}/>}

            </a>
        </div>;

        const pointerContent = <div className="preview-cur before-n">
            <img src={pointer.path ? pointer.path: "/assets/images/icons/pointer-icon.png"} alt="Pointer" onClick={() => this.setNativeInput('pointer', id)}/>
            <a className="button action button__upload"
               title={chrome.i18n.getMessage('hint_upload_pointer')}
               onClick={() => !pointer.path ?this.setNativeInput('pointer', id) : this.deletePointer() }>
                {pointer.path ? <FontAwesomeIcon icon={faTrash}/> : <FontAwesomeIcon icon={faPlus}/>}

            </a>
        </div>;

        return <div className="col-lg-2 col-md-3 col-sm-4 col-xs-6 box-preview-cur mb20" data-id={id}>
            {cursorContent}
            {pointerContent}
        </div>
    }
}


