import React, {Component} from 'react';
import Nouislider from "nouislider-react";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faTimes} from "@fortawesome/fontawesome-free-solid";

class SizeSlider extends Component {
    constructor(props) {
        super(props);
        this.setState(...props);
    }

    render() {
        const {size, maxSize, popupSetting, toogleSize, onSlide} = this.props;
        return (
            <div className={popupSetting === false ? 'setting-size ' : 'setting-size active'}>
                <Nouislider id="popup-size"
                            range={{min: 0, max: 7}}
                            step={1}
                            start={size}
                            orientation="horizontal"
                            onSlide={onSlide}
                            pips={{mode: 'steps', density: 20}}
                            behaviour={'tap'} connect={[true, false]}/>
                <a className="close-setting-size" rel="noreferrer" onClick={toogleSize}>
                    <FontAwesomeIcon icon={faTimes}/>
                </a>
            </div>
        );
    }
}

export default SizeSlider;


